<template>
  <div class="invited-list">
    <div id="invited">
      <p class="invited-text">我（ID {{mId}}）已累计成功邀请用户</p>
      <span class="invited-num"><strong>{{invitedCount}}</strong>个</span>
    </div>
    <van-list v-model="isLoading" :finished="isFinished" :finished-text="!isInit && (!invitedList || invitedList.length === 0) ? '' : '没有更多了'" @load="fetchList">
      <ul class="list" v-if="!isInit && (invitedList && invitedList.length>0)">
        <li class="item" v-if="item in item.list" :key="item.id">
          <div class="head-box">
            <div class="head img-box">
              <!-- <img src="http://imgtest.hellobuluo.com/94deed3f254e9cd9bc0c391a7f213ceb/5eae7f9c/888481/head/12b7a08766954f008cc114b3cb4847fb/b1d35f71" alt="" class="img" /> -->
              <img :src="item.headPicUrl" alt="" class="img" />
            </div>
            <span class="nickname">{{item.nickName}}</span>
          </div>
          <span class="status">{{item.firstAllAuth ? '已认证证件' : item.firstBaseAuth ? '已完善资料' : '已注册'}}</span>
        </li>
      </ul>
    </van-list>

    <div class="no-invited" v-if="!isInit && (!invitedList || invitedList.length === 0)">
      <p class="no-message-tips">当前暂无邀请的人</p>
      <img src="~@/assets/img/no-search-result.png" alt="" class="no-message-img" />
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'invitedList',
  data () {
    return {
      // list: [],
      isInit: true,
      isLoading: false,
      isFinished: false
    }
  },
  computed: {
    ...mapState('invite', {
      invitedCount: 'invitedCount',
      currentPage: 'currentPage',
      invitedList: 'invitedList'
    }),
    ...mapGetters({
      mId: 'mId'
    })
  },
  methods: {
    ...mapActions('invite', {
      fetchInvitedList: 'fetchInvitedList'
    }),
    fetchList () {
      this.isLoading = true
      this.fetchInvitedList({
        currentPage: this.currentPage,
        pageSize: 20
      }).then(res => {
        this.isLoading = false
        if (res.code === '0000') {
          if (this.currentPage >= Math.ceil(this.invitedCount / 20)) {
            this.isFinished = true
          }
        } else {
          this.isFinished = true
          this.$toast(res.msg)
        }
      }, err => {
        this.isLoading = false
        this.isFinished = true
        this.$toast(err)
      }).finally(() => {
        this.isInit = false
        this.isLoading = false
      })
    }
  }
}
</script>

