  padding: 28px 40px;
  // 已邀请的人的行高
  $invitedHeight: 64px;
  #invited {
    display: flex;
    flex-flow: row  nowrap;
    justify-content: space-between;
    padding: 0 28px;
    color: #F57949;
    border: 1px solid #FD995A;
    border-radius: $invitedHeight;
    background: #FFFAEA;
  }
  .invited-text {
    line-height: $invitedHeight;
  }
  .invited-num {
    display: flex;
    align-items: center;
    line-height: $invitedHeight;
    strong {
      font-size: 46px;
    }
  }
  .list {
    margin-top: 42px;
  }
  .item {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }
  .head-box {
    display: flex;
    flex: 1 1 auto;
    align-items: center;
    height: auto;
  }
  .head {
    width: 66px;
    height: 66px;
    border: 1px solid #979797;
  }
  .nickname {
    margin-left: 18px;
    color: #A4A4A4;
  }
  .status {
    color: #A4A4A4;
  }
}
</style>