<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'invite',
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapState('invite', {
      invitedCount: 'invitedCount'
    })
  },
  data () {
    return {
      loadedImgCount: 0
    }
  },
  methods: {
    ...mapActions('invite', {
      fetchInvitedList: 'fetchInvitedList'
    }),
    toInvitedList () {
      this.$router.push('/invited-list.html')
    },
    draw () {
      const $img = document.querySelector('#invite-bg')
      const cav = document.querySelector('#canvas')
      if (cav) {
        this.drawContent(cav, $img, '长按保存图片后分享给好友吧~')
      } else {
        return this.$toast('获取邀请二维码失败，请联系豆子姐处理')
      }

      const cavSave = document.querySelector('#canvas-save')
      if (cavSave) {
        this.drawContent(cavSave, $img, '长按识别进入hello部落')
      } else {
        return this.$toast('获取邀请二维码失败，请联系豆子姐处理')
      }
      const inviteSave = document.querySelector('#invite-save')
      inviteSave.src = cavSave.toDataURL("image/png")
    },
    drawContent (cav, $img, words) {
      const context = cav.getContext('2d')
      const imgWidth = $img.width
      const imgHeight = $img.height
      cav.width = imgWidth
      cav.height = imgHeight
      context.clearRect(0, 0, imgWidth, imgHeight)
      context.drawImage($img, 0, 0, imgWidth, imgHeight)

      const code = document.querySelector('#qrCode')
/*       const codeWidth = code.width
      const codeHeight = code.height */
      const drawSize = imgWidth / (375 / 72)
      // 263 = 375 - (72 + 20)； 总宽度 - 二维码的宽度加上右边距 = 左边距
      context.drawImage(code, imgWidth / (375 / 283), imgHeight - (144 + 260), drawSize, drawSize)
      cav.style.width = '100%'

      // 绘制文字
      context.fillStyle = '#a4a4a4'
      context.textAlign = 'right'
      // 750的设计稿上字体为24px，所以要计算出在画布上的比例
      context.font = imgWidth / (375/12) + `px 'PingFangSC-Regular', 'PingFang SC', 'sans-serif', 'Arial'`
      context.fillText(words, imgWidth - 80, imgHeight - 50)
    }
  },
  mounted () {
    const code = document.querySelector('#qrCode')
    const $img = document.querySelector('#invite-bg')
    // 背景图和二维码都加载完毕后再调用画图
    code.onload = () => {
      if (this.loadedImgCount >= 1) {
        this.draw()
      } else {
        this.loadedImgCount++
      }
    }

    $img.onload = () => {
      if (this.loadedImgCount >= 1) {
        this.draw()
      } else {
        this.loadedImgCount++
      }
    }

    if (this.invitedCount === undefined) {
      this.fetchInvitedList()
    }
  }
}
</script>

